import React from "react";
import { makeStyles } from '@material-ui/core/styles';

import {
    Box,
    Grid,
    Button,
    List,
    ListItem,
    Dialog,
    Slide,
    Divider,
    IconButton,
    Typography,
    AppBar,
    Toolbar,
    TextField,
    Avatar,
    Popover,
} from '@material-ui/core';

import {
    Close as CloseIcon,
    ShoppingCart as ShoppingCartIcon,
    Add as AddIcon,
    Remove as RemoveIcon,
    LocalShipping as LocalShippingIcon,
    Security as SecurityIcon,
    Info as InfoIcon,
    HelpOutline as HelpOutlineIcon,
} from '@material-ui/icons/';

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: 'relative',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
    quantityField: {
        width: "100px",
        '& input': {
            direction: "rtl",
        },
    },
    productLine: {
        padding: theme.spacing(4),
    },
    avatar: {
        marginRight: theme.spacing(2),
    },
    avatarShipping: {
        color: "#ffaa08",
        backgroundColor: "transparent",
    },
    avatarInfo: {
        backgroundColor: "transparent",
    },
    avatarShield: {
        color: "#ffaa08",
        backgroundColor: "transparent",
    },
    commentField: {
        [theme.breakpoints.up('sm')]: {
            width: "70%",
        },
        [theme.breakpoints.only('xs')]: {
            width: "90%",
        },
    },
    popover: {
        padding: theme.spacing(2),
    }
}));

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

function ProductDetail(props) {
    const { products, counters, openCart, handleClose, onChange, comment, handleComment, handleCheckout, uiDisabled } = props;
    const classes = useStyles();

    const [anchorEl, setAnchorEl] = React.useState(null);

    const popoverClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const popoverClose = () => {
        setAnchorEl(null);
    };

    const open = Boolean(anchorEl);
    const id = open ? 'simple-popover' : undefined;

    let productsFormatted = products;

    let vat = {
        "masque": 1.055,
        "gel": 1.055,
        "gant": 1.055,
        "surblouse": 1.055,
        "lunettes": 1.055,
        "visire": 1.055,
        "thermomtre": 1.2
    };

    const calcTTC = (price, quantity, nature) => {

        let htPrice = (price * quantity).toFixed(3).slice(0, -1);
        let ttcPrice = (Number(htPrice) * vat[nature]).toFixed(3).slice(0, -1);

        return ttcPrice;
    }

    products.map((item, index) => {
        if (counters[item.sku] == undefined) return;

        productsFormatted[index].quantity = counters[item.sku];
        productsFormatted[index].finalPrice = ((Number(productsFormatted[index].price) * Number(productsFormatted[index].quantity)) * vat[productsFormatted[index].nature]).toFixed(3).slice(0, -1);

        return;
    });

    let cart = productsFormatted.reduce((a, b) => ({ finalPrice: Number(a.finalPrice) + Number(b.finalPrice) }));

    cart.finalPrice = Number(cart.finalPrice).toFixed(3).slice(0, -1);

    return (
        <div>
            <Dialog fullScreen open={openCart} onClose={handleClose} TransitionComponent={Transition}>
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
                            <CloseIcon />
                        </IconButton>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="cart">
                            <ShoppingCartIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            Vue d'ensemble du panier
                        </Typography>
                    </Toolbar>
                </AppBar>
                {productsFormatted.map((item, index) => {
                    return item.quantity > 0 &&
                        <>
                            <Grid container alignItems="center" className={classes.productLine} key={index}>

                                <Grid item xs={12} sm={6} style={{ marginBottom: "15px" }}>
                                    <Grid container alignItems="center" spacing={2}>
                                        <Grid item>
                                            <Avatar alt={item.images[0].name} src={item.images[0].url} />
                                        </Grid>
                                        <Grid item>
                                            <Typography variant="body1">
                                                {item.name} {item.type && `(${item.type})`}
                                            </Typography>
                                            <Typography variant="body2">
                                                {item.unitPrice}{item.currency} H.T. / {item.nature}
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                    <Grid
                                        container
                                        justify="flex-end"
                                        alignItems="center"
                                    >
                                        <Grid item style={{ marginRight: "15px" }}>
                                            <IconButton
                                                aria-label="enlever 1"
                                                disabled={uiDisabled}
                                                onClick={(event) => onChange(item.sku, parseInt(counters[item.sku]) - 1)}
                                            >
                                                <RemoveIcon />
                                            </IconButton>
                                            <TextField
                                                className={classes.quantityField}
                                                label={parseInt(item.batch) > 1 ? `Bote de ${item.batch}` : `Pice`}
                                                size="small"
                                                onChange={(event) => onChange(item.sku, event.target.value)}
                                                value={counters[item.sku]}
                                                InputLabelProps={{
                                                    shrink: true,
                                                }}
                                                variant="outlined"
                                            />
                                            <IconButton
                                                aria-label="ajouter 1"
                                                disabled={uiDisabled}
                                                onClick={(event) => onChange(item.sku, parseInt(counters[item.sku]) + 1)}
                                            >
                                                <AddIcon />
                                            </IconButton>
                                        </Grid>
                                        <Grid item>
                                            <Typography>
                                                {`${calcTTC(item.price, item.quantity, item.nature)}${item.currency}`}
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Divider variant="middle" />
                        </>
                })}
                <Grid container alignItems="center" className={classes.productLine}>
                    <Grid item xs={12} sm={8} md={9} xl={10}>
                        <Grid container alignItems="center" spacing={2}>
                            <Grid item>
                                <Avatar className={classes.avatarShipping}>
                                    <LocalShippingIcon />
                                </Avatar>
                            </Grid>
                            <Grid item>
                                <Typography variant="body1" component="span">
                                    Frais de livraison
                                </Typography>
                                <IconButton aria-label="info" className={classes.popoverIcon}>
                                    <HelpOutlineIcon onClick={popoverClick} />
                                </IconButton>
                                <Popover
                                    id={id}
                                    open={open}
                                    anchorEl={anchorEl}
                                    onClose={popoverClose}
                                    style={{ width: "80%" }}
                                    anchorOrigin={{
                                        vertical: 'bottom',
                                        horizontal: 'center',
                                    }}
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'center',
                                    }}
                                >
                                    <Typography className={classes.popover}>En raison de dlais d'approvisionnement, veuillez prvoir un dlai de livraison d'une quinzaine de jours. Nous travaillons  flux tendu pour vous offrir les tarifs les plus bas.</Typography>
                                </Popover>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} sm={4} md={3} xl={2}>
                        <Grid container
                            direction="column"
                            justify="center"
                            alignItems="flex-end"
                        >
                            <Grid item xs={12}>
                                <Box style={{ textAlign: "center" }}>
                                    <Typography variant="body1">OFFERTS</Typography>
                                </Box>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <Divider variant="middle" />
                <Grid container alignItems="center" className={classes.productLine}>
                    <Grid item xs={12} sm={8} md={9} xl={10}>
                    </Grid>
                    <Grid item xs={12} sm={4} md={3} xl={2}>
                        <Grid container
                            direction="column"
                            justify="center"
                            alignItems="flex-end"
                        >
                            <Grid item xs={12}>
                                <Box style={{ textAlign: "center" }}>
                                    <Typography variant="body1">Total T.T.C. : {cart.finalPrice}</Typography>
                                </Box>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>

                <List>
                    <ListItem style={{ justifyContent: "center", marginTop: "20px" }}>
                        <Button
                            size="large"
                            variant="contained"
                            color="primary"
                            disabled={cart.finalPrice < 2500}
                            onClick={handleCheckout}>
                            Commander
                        </Button>
                    </ListItem>
                    {cart.finalPrice < 2500 &&
                        <ListItem style={{ justifyContent: "center" }}>
                            <Avatar className={classes.avatarInfo}>
                                <InfoIcon color="primary" />
                            </Avatar>
                            <Typography variant="body2">
                                Minimum de commande : 2500.00 T.T.C.
                            </Typography>
                        </ListItem>
                    }
                    <ListItem style={{ justifyContent: "center" }}>
                        <Avatar className={classes.avatarShield}>
                            <SecurityIcon />
                        </Avatar>
                        <Typography variant="body2" component="span" style={{ fontStyle: "italic" }}>
                            Nous ne stockons pas vos donnes bancaires.
                        </Typography>
                    </ListItem>
                    <ListItem style={{ justifyContent: "center" }}>
                        <TextField
                            className={classes.commentField}
                            id="comment-textfield"
                            label="Veuillez indiquer vos commentaires ou demandes spcifiques (taille, etc)"
                            multiline
                            rows={2}
                            rowsMax={4}
                            value={comment}
                            onChange={(event) => handleComment(event.target.value)}
                        />
                    </ListItem>
                </List>
            </Dialog>
        </div >
    );
}

export default ProductDetail;
