const html = '<main role="main"> <div class="d-flex" id="wrapper"> <div id="page-content-wrapper" class="order-1 order-lg-2"> <div class="container-fluid tab-content" id="v-pills-tabContent"> <div class="tab-pane fade show active" id="introduction" role="tabpanel" aria-labelledby="introduction-tab"> <h2>Schma Directeur Parcours Patient COVID</h2> <p>Mise en uvre et fonctionnement dun centre COVID de tri et de dtection dans le cadre dun suivi global patient.</p><p>Ce prsent document est interactif,  objectif dusage collectif, au service de tous les acteurs, est propos en OPEN SOURCE.</p><p>Il est issu dun travail collgial impuls et coordonn par OPTION PUBLIC.</p></div><div class="tab-pane fade show" id="objectifs" role="tabpanel" aria-labelledby="objectifs-tab"> <h2>Les objectifs, principes et enjeux des centres de tri et de dtection</h2> <p>Face  lampleur de la pandmie, il est ncessaire dapporter une rponse commune, cohrente et pertinente. Pour ce faire, plusieurs scnarios sont proposs afin de rendre les services de sant plus efficients.</p><p>Le principe est de faire venir vers le Centre Covid les malades ambulatoires qui prsentent une possibilit dtre atteints. (Information positive.) Les patients seront adresss par les mdecins traitants ou le 15.</p><p>Ainsi, il est ncessaire doprer une rationalisation du cycle de soin, reprogrammation du mdecin de ville, cration dun parcours de soins spcifique afin de limiter la propagation du virus. <ul style="list-style: circle"> <li>Tri mdicalis : renvoi vers le systme libral des patients ngatifs ou  surveiller, soins aux malades graves (circuit-court-urgent : interfaage avec le 15).</li><li>Organisation dune filire de surveillance des patients  risque avec tlconsultation, passage de personnels soignants pour prise de constantes et suivi.</li><li>Mise en place de filires dvacuation vers un centre hospitalier des patients dont ltat se dgrade.</li></ul> </p><p>Lefficacit du Centre repose sur la pertinence du tri  lentre et sur sa capacit  renvoyer vers la Ville dans les meilleures conditions les personnes qui ne prsentent pas de forme svre. En quelque sorte, il sagit de mettre en place des chanes de soins spcialises: <ul style="list-style: circle"> <li>Aptes  traiter des cas accidentels urgents exceptionnels.</li><li>Capables d\'orienter les patients et dorganiser leur suivi.</li><li>Pouvant renvoyer vers la mdecine de ville et ce, ds le premier sas, les cas ne relevant pas de leur mission dans les meilleures conditions grce au couplage mdico-social assur par le CCSS.</li><li>Mais surtout tendues vers leur mission qui est de mtaboliser rapidement et compltement les cas-cibles.</li></ul> </p><p><b>Fiable</b>: Le processus est capable daccueillir et dorienter TOUS les patients atteints ou suspects dtre contamins. Il prcise les conditions de cette fiabilit: <ul style="list-style: circle"> <li>Une fiabilit rduite compte tenu des contingences administratives actuelles.</li><li>Une fiabilit idale qui suppose des conditions exorbitantes du droit commun, en prcisant les options pouvant tre choisies.</li><li>Il ne traite pas du traitement interne  lhpital.</li></ul> </p><p><b>Rsilient</b>: Le processus se doit dtre scalable: <ul style="list-style: circle"> <li>En quantit: pourra sadapter  laugmentation du nombre de cas en mobilisant tous les moyens possibles dans et hors des systmes de sant.</li><li>En proposant des options de drogation exorbitantes du droit commun des normes administratives et de gestion publique de droit commun.</li></ul> </p><p><b> ce titre, ce guide prsente 4 types de structures, le pivot tant bien le principe de la prise en charge en amont pour soulager la pression sur la mdecine de ville et hospitalire </b> <ul> <li><b>Centre de tri : trier ds lamont les profils  risque pour optimiser le suivi  domicile ou lenvoi vers lhpital.</b></li><li><b>Centre de dtection : collectiviser ds lamont le processus de dtection pour optimiser la prise en charge de sant.</b></li><li>Centre de confinement : isoler les personnes dtectes, notamment pour viter une contamination des familles dans le cadre dun retour  domicile.</li><li>Cellule de crise : organiser, principalement en Mairie ou en interco, larticulation des moyens physiques et humains pour monter ces centres, les approvisionner, assurer leur rgularit administrative, leur fonctionnement financier et exploiter les donnes releves.</li></ul> </p></div><div class="tab-pane fade show" id="macro" role="tabpanel" aria-labelledby="macro-tab"> <h2>Chane de valeur et Macro  Processus cibles permettant de cadrer les primtres de responsabilits</h2> <h3 id="macroPart1">1. Schma Gnrique du parcours patient</h3> <p>Les tapes  standard  dun parcours patient sont matrialises par la chane de valeur* prsente ci-dessous.</p><img class="d-block mx-auto" width="90%" src="images/assets/images/parcours1.png"> <p>Ce modle servira de socle pour dcrire les particularismes proposs de prise en charge particulire du patient COVID.</p><p>*Source : capitalisation Portfolio Consulting Group 2020</p><div class="line"></div><h3 id="macroPart2">2. Schma Spcifique du parcours patient COVID</h3> <img class="d-block mx-auto" width="90%" src="images/assets/images/parcours2.png"> <p>Le zoom sur chacun des blocs de la chane de valeur COVID permet de dfinir les processus qui vont conduire au dimensionnement des centres</p></div><div class="tab-pane fade show" id="description" role="tabpanel" aria-labelledby="description-tab"> <h2>Description du fonctionnement des centres et de leur articulation dans le circuit global</h2> <h3 id="descriptionPart1">1. Processus du Centre de TRI</h3> <p>Le centre de TRI se situe en amont du processus global. Son bon fonctionnement optimise le fonctionnement de lensemble de la chane.</p><img class="d-block mx-auto" width="90%" src="images/assets/images/parcours3.png"> <p>Les impacts volumtriques et spcifiques du COVID font porter des risques de fiabilit sur le processus quil faut aborder ds sa constitution</p><p>Source : capitalisation Portfolio Consulting Group et OPTION PUBLIC 2020</p><div class="line"></div><h3 id="descriptionPart2">2. Points critiques de fiabilit du Centre de Tri COVID-19</h3> <p>Le parcours optimis dun patient COVID est matrialis par le passage de structures et de modalits spcifiques et ad hoc, ainsi que par un impact volumtrique et particulier sur le dispositif standard.</p><img class="d-block mx-auto" width="90%" src="images/assets/images/table1.png"> <div class="line"></div><h3 id="descriptionPart3">3. Processus du Centre de DEPISTAGE</h3> <p>Le centre de DEPISTAGE permet de soulager dautant plus le fonctionnement de la chane en aval. Il demande une structure renforce en termes de moyens mdicaux.</p><img class="d-block mx-auto" width="90%" src="images/assets/images/parcours4.png"> <p>Afin de contribuer  rguler les risques de contamination des familles dans le cadre dun retour  domicile, le dispositif peut tre complt dun centre de confinement</p><p>Source : capitalisation Portfolio Consulting Group et OPTION PUBLIC 2020</p><div class="line"></div><h3 id="descriptionPart4">4. Processus du Centre de Confinement</h3> <p> <ul style="list-style: circle"> <li>Dans la possibilit o ltat de sant des patients ncessite un confinement total, on peut mettre en place un centre de confinement pour contenir linfection et la matriser. Ce dispositif peut empcher les personnes vivant en communaut de transmettre le virus  leur entourage et damliorer la surveillance des malades</li><li>La mise en place de ce dispositif ne peut se faire que sil y a une politique de dpistage des patients qui est mis en place.</li></ul> </p><img class="d-block mx-auto" width="90%" src="images/assets/images/parcours5.png"> <p>Ce dispositif spcifique applicable en hyper  crise doit faire lobjet dune description dtaille le cas chant qui nest pas lobjet de ce document</p><p>Source : OPTION PUBLIC 2020</p><div class="line"></div><h3 id="descriptionPart5">5. Schma Gnrique du parcours patient : Dplacement des patients vers les hpitaux</h3> <p>Dans le cadre de lextraction et du dplacement des patients atteints du COVID-19 il serait pertinent de rserver des ambulances ddies aux patients du Covid -19. Cela permettrait de rduire les risques de transmissions et damliorer la clrit des flux.</p><img class="d-block mx-auto" width="90%" src="images/assets/images/parcours6.png"> <p>Lensemble du circuit de dplacement POST TRI et DETECTION doit tre pens autant que possible spar du circuit standard</p><p>Source : capitalisation Portfolio Consulting Group et OPTION PUBLIC 2020</p><div class="line"></div><h3 id="descriptionPart6">6. Schma Gnrique du parcours patient : Suivi  domicile</h3> <p>Au sein du suivi  domicile des patients autonomes et peu autonomes qui sont possiblement atteints du COVID -19 , il est ncessaire de mettre en place deux schmes de suivi des patients selon la typologie : Autonome & Peu Autonome.</p><img class="d-block mx-auto" width="90%" src="images/assets/images/parcours7.png"> <p>Lefficacit du suivi  domicile est renforce sil peut prendre appui sur une chane numrique permettant une coordination des moyens et des interventions cibles </p></div><div class="tab-pane fade show" id="organisation" role="tabpanel" aria-labelledby="organisation-tab"> <h2>Organisation, moyens matriaux et humains du centre de TRI</h2> <h3 id="organisationPart1">1. Principes de fonctionnement du centre de TRI </h3> <p>Les personnes qui se prsentent sont accueillies par un personnel non mdical qui les quipe, les installe dans la chane dattente en respectant les distances de scurit.</p><p>Chaque personne est vue par une infirmire de prconsultation, qui prend sa temprature et ses constantes  distance, lui pose les questions cls et value rapidement si elle est susceptible dtre positive. Plusieurs possibilits se prsentent: <ul style="list-style: circle"> <li>Si on juge que ce nest pas le cas, on la renvoie avec des renseignements fournis par le rfrent social dpendant du CCSS qui se trouve sur la chane de sortie  ngatif .</li><li>Si son cas est suspect, on la fait passer dans la chane dvolue  ces cas. (Identification par marquage color.) Le malade est examin, sort sil est jug indemne par une sortie spcifique ou sinon rintroduit dans le circuit Covid.</li><li>Si la personne est potentiellement atteinte du Covid-19, elle est dirige dans la zone ddie. (Identification par marquage color).</li><li>Si son cas est douteux, on la fait passer dans la chane dvolue  ces cas. (Identification par marquage color.) Le malade est examin, et orient vers 3 circuits diffrents: un confinement simple avec surveillance autonome et consignes de scurit, un confinement avec passage infirmier pour surveillance dun professionnel et valuation clinique. Evacuation vers un centre hospitalier.</li></ul> </p><p>Le centre de tri organise le suivi de patients ainsi qu\' une filire dvacuation de ceux-ci si dgradation de ltat gnral.</p><p>Un circuit-court-urgent est prvu en cas de malaise, de cas qui saggraveraient brutalement, avec une zone destine  recevoir ce type de malade. La liaison avec la rgulation15 est prvue dans le schma de fonctionnement du CCSS. <ul> <li>Les Cabinets Mdicaux dexamen de la zone Covid reoivent les malades par ordre darrive, un collationnement des renseignements administratifs est prvu.</li><li>Un examen par un mdecin quip de protections prconises, confirme le diagnostic, value la gravit du cas, conduit  une ordonnance prpare, on raye les mentions inutiles (viter les surcharges).</li><li>Passage au pharmacien lui aussi quip de protections, recueil des fiches administratives.</li><li>Sortie par un accs spcifique.</li><li>Organisation du suivi des patients soit par tlsurveillance soit par passage des IDELS.</li><li>Un couplage permanent avec le CCSS permet de grer les urgences, les demandes d\'hospitalisation, les transports et l\'approvisionnement des patients, et de prendre en charge leurs problmes sociaux.</li></ul> </p><div class="line"></div><h3 id="organisationPart2">2. Description dtaille du Centre de Tri et Dpistage</h3> <p>Cette reprsentation permet de faire le lien entre les fonctionnalits et les moyens  chaque tape.</p><p>Remarque : il est considr que le Dpistage est une fonction complmentaire du Centre de TRI.</p><img class="d-block mx-auto" width="95%" src="images/assets/images/schema1.png"> <p>La description des moyens par fonction permettra de dimensionner les besoins  chaque tape.</p><div class="line"></div><h3 id="organisationPart3">3. Expression des besoins et Organisation Cible du centre de TRI</h3> <p>Principales fonctions du Centre <ul> <li>Accs au centre</li><li>Matriel</li><li>Fonctionnement interne</li><li>Nettoyage et dcontamination des lieux et du mobilier</li><li>Gestion des dchets (mnagers et DASRI)</li></ul> </p><p>Comment communiquer vers la population lexistence du centre Covid-19 ? Faire un plan de communication sur lexistence du centre covid-19 <ul> <li>Par qui?</li><li>Quels lments de langage?</li><li>Moyens de diffusion? <ul style="list-style: circle"> <li>Sms</li><li>Voie daffichage administratif</li><li>Distribution dans botes aux lettres</li><li>Autres?</li></ul> </li><li>Frquence de diffusion?</li></ul> </p><div class="line"></div><h3 id="organisationPart4">4. Centre Covid-19  Expression des besoins et Organisation Cible  Des locaux adapts </h3> <p>Lieu(x) comprenant un espace extrieur et intrieur suffisamment grand pour que les distances de scurit dites  distanciation sociale  soit  minima respectes.</p><p>Lieu(x) accessible(s) facilement en voiture comme  pied, de surcrot galement accessible(s) aux personnes ayant un handicap.</p><p>Lieu(x) disposant dun parking de stationnement intgr ou  proximit directe.</p><p>Lieu(x) disposant de plusieurs points daccs permettant de mettre en place,  bonne distance, des flux distincts  entrants  et  sortants   lextrieur comme  lintrieur des locaux: <ul style="list-style: circle"> <li>Pour les vhicules: un point dentre, de stationnement (1 emplacement sur 2 sera vide de tout vhicule) et un point de sortie</li><li>Pour les pitons: un point dentre et un point de sortie distinct et suffisamment loign lun de lautre si ces points ne peuvent tre  loppos lun de lautre. Ces accs pitons doivent disposer de rampes permettant laccessibilit aux locaux.</li></ul> </p><p>Lieu(x) disposant de points deau rpartis et en nombre suffisant.</p><p>Lieu(x) disposant de vestiaires hommes et de vestiaires femmes, de douches et de sanitaires en nombre suffisant.</p><p>Lieu(x) disposant dun espace scuris pour stocker le matriel mdical (fermeture  cl  minima)</p><p>Lieu(x) disposant dun espace ddi  la collecte et  lentreposage des dchets mnagers et surtout des DASRI. Cet espace doit tre excentr et directement accessible par le personnel de lentreprise de collecte des dchets.</p><p>Lieu(x) disposant dun espace dune superficie suffisante, pour respecter la distanciation sociale [ dfaut, limiter le nombre de personnes prsentes en mme temps], qui sera ddi au personnel du centre covid-19 pour les pauses et la restauration.</p><p>Un espace ddi au stationnement dune ambulance pour lvacuation de patients vers lhpital/clinique.</p><div class="line"></div><h3 id="organisationPart5">5. Centre Covid-19  Expression des besoins et Organisation Cible  Organisation daccs au Centre Covid-19</h3> <p>A chaque point daccs  entrants , un personnel daccueil et dorientation sera prsent, idem pour le stationnement un personnel indiquera la place o devra seffectuer le stationnement du vhicule.</p><p>A chaque pont daccs, un affichage dinformations sera prsent.</p><p>Les flux  entrants  et  sortants  seront matrialiss par un barrirage en fixe de type  vauban  depuis la priphrie du btiment jusquaux points daccs de celui-ci: chaque circuit devra porter un affichage prcisant  flux entrant  /  flux sortant </p><p>Dans chacun des circuits, une distanciation sociale devra tre respecte par les patients accdant ou sortant du centre Covid-19.</p><div class="line"></div><h3 id="organisationPart6">6. Centre Covid-19  Expression des besoins et Organisation Cible  Matriel ncessaire au fonctionnement du Centre Covid-19</h3> <p><span style="text-decoration: underline">Espace daccueil et dattente :</span> des chaises, des tables, des panneaux daffichage sur pieds destins  linformation des personnes, des poubelles et leurs sacs, un ordinateur avec une application prenant en compte les informations concernant le patient. Le patient devra remplir une fiche de ce <a href="images/assets/documents/DOSSIER_SOINS_IDELCOVID.pdf" download="DOSSIER_SOINS_IDELCOVID.pdf" target="_blank">format-l</a>.</p><p><span style="text-decoration: underline">Espace dattente devant le box du mdecin :</span> des chaises, des poubelles et leurs sacs.</p><p><span style="text-decoration: underline">Espace de consultation privatif ou box  mdecin  :</span> pour crer ces box, il faut des parois amovibles ou paravents permettant une consultation en toute confidentialit en labsence de bureaux prexistants pouvant assurer cette fonction: <ul> <li>Des chaises (1 pour le mdecin, 2 pour les patients)</li><li>Une table de consultation</li><li>Une table et un ordinateur comprenant lapplication de gestion du patient</li><li>Une poubelle normale et une poubelle DASRI</li></ul> </p><p><span style="text-decoration: underline">Espace de repos et de restauration des quipes du centre covid-19 :</span> tables, chaises, poubelles, points deau potable, frigidaire, etc..</p><div class="line"></div><h3 id="organisationPart7">7. Matriel  prvoir pour quiper les centres de consultations</h3> <p> <ul style="list-style: circle"> <li>Des paravents pour dlimiter les zones / des bandes adhsives de marquage au sol</li><li>Du matriel daffichage et de signaltique du matriel informatique, une connexion internet, des feuilles de papier, des stylos</li><li>Organisation de l vacuation des dchets</li><li>Organiser un service de blanchisserie pour le linge  laver et des serviettes pour la douche</li><li>Prvoir un local propre pour le stockage de matriel</li><li>Un local  sale  pour entreposer les dchets en attente dvacuation</li><li>Des imprimantes pour imprimer ordo, consignes de confinement, fiches de suivi etc etc</li><li>Deux lits et un brancard, ainsi que des protections  usage unique, des lits dexamens</li><li>Quatre tables</li><li>Dix chaises</li><li>Cinq poubelles quipes en permanence de sacs (une fois utiliss, les sacs ferms seront ensachs dans un autre sac) ainsi que des containers situs  lextrieur du btiment</li><li>Trois fauteuils destins aux personnes en attente dhospitalisation</li><li>Des pulvrisateurs de produit nettoyant ainsi que des lingettes pour assurer la dsinfection du matriel</li><li>Du savon liquide, des essuie-mains  usage unique et des solutions hydro alcooliques pour le lavage des mains</li><li>Des gants, des masques FFP2, des lunettes de protection ainsi quune sur blouse</li><li>Du matriel pour la prise de constantes qui restera au centre</li><li>Le mdecin sera quip de sa trousse de consultation</li></ul> </p><div class="line"></div><h3 id="organisationPart8">8. Centre Covid-19  Expression des besoins et Organisation Cible  Fonctionnement du Centre Covid-19</h3> <p>Espace daccueil et dattente : <ul style="list-style: circle"> <li>Dans cet espace daccueil, les personnes sont accueillies par un secouriste qui leur demande de procder au lavage des mains (point deau ou solution hydro alcoolique)</li><li>Ensuite elles attendent de rencontrer une infirmire pour un 1er bilan et prise des informations ncessaires (nom, prnom, etc)</li><li>Enfin elles sont orientes vers le box de mdecin</li></ul> </p><p>Espace dattente devant le box du mdecin</p><p>Espace de consultation avec le mdecin: le mdecin renseigne lapplication y compris pour le suivi du patient</p><p>Sortie du patient  lissue de la consultation: 3 cas possibles <ul style="list-style: circle"> <li>Sortie pour confinement  la maison (peu de symptmes, mais avec un suivi sur lvolution)</li><li>Sortie pour confinement  la maison avec un suivi de type  hospitalisation  domicile </li><li>Sortie avec vacuation vers un hpital/clinique</li></ul> </p><p>Restauration des quipes du centre covid-19: plusieurs possibilits:  dfinir <ul style="list-style: circle"> <li>Repas froids livrs par une socit de restauration</li><li>Repas personnels apports par chacun: lespace ddi doit tre quip dun frigidaire</li></ul> </p><div class="line"></div><h3 id="organisationPart9">9. Centre Covid-19  Expression des besoins et Organisation Cible Nettoyage et dcontamination du Centre Covid-19</h3> <p>Les lieux (sanitaires, douches, vestiaires, box..) comme le mobilier, doivent tre nettoys et les surfaces dcontamines:</p><p>Il appartiendra  chaque responsable de centre de prvoir ces ressources en fonction des disponibilits locales</p><p> <ul> dfinir par les gestionnaires du centre ou les lus locaux <li>Par qui? <ul style="list-style: circle"> <li>Personnel de la collectivit ou socit de nettoyage</li></ul> </li><li>Frquence de passage? <ul style="list-style: circle"> <li>Pour les locaux: 2 fois/jours ou plus selon la superficie des locaux et le nombre de personnes accueillies</li><li>Pour les surfaces: toutes les heures</li></ul> </li><li>Avec quels produits <ul style="list-style: circle"> <li> dfinir sur zone en fonction des ressources ncessaires</li></ul> </li><li>Stockage des produits: <ul style="list-style: circle"> <li>O: sur place dans local scurit</li><li>Prvoir des bacs de rtention si produits dangereux stocks sur place en quantit</li></ul> </li></ul> </p></div><div class="tab-pane fade show" id="plans" role="tabpanel" aria-labelledby="plans-tab"> <h2>Plan physiques de fonctionnement des Centres</h2> <h3 id="plansPart1">1. Plan gnrique de Centre de dtection dans lurbanisme</h3> <img class="d-block mx-auto" width="70%" src="images/assets/images/plan1.jpg"> <div class="line"></div><h3 id="plansPart2">2. Plan de dambulation dun centre de Dpistage</h3> <img class="d-block mx-auto" width="70%" src="images/assets/images/schema2.png"> <div class="line"></div><h3 id="plansPart3">3. Plan de dambulation dun centre de Dpistage</h3> <img class="d-block mx-auto" width="70%" src="images/assets/images/schema3.png"> <div class="line"></div><h3 id="plansPart4">4. Plan physique dorganisation du centre de dpistage (exemple)</h3> <img class="d-block mx-auto" width="70%" src="images/assets/images/schema4.png"> </div><div class="tab-pane fade show" id="realisation" role="tabpanel" aria-labelledby="realisation-tab"> <h2>Exemples de ralisation et de directives en cours</h2> <h3 id="realisationPart1">1. Centre Covid-19  Exemple de Centre  Centre de consultations pluridisciplinaire  Moissac (82)</h3> <p>Afin de dsengorger lhpital et de faire en sorte que les personnes pouvant tre atteintes par le Covid-19, prsentant des symptmes mineurs, ne soient pas en relation avec les autres patients des cabinets mdicaux ou les urgences du CHIC, la commune de Moissac  cr un centre de consultations Covid-19.</p><p>A linitiative des mdecins du groupe mdical Pasteur, prs de 90 professionnels de sant (mdecins, pneumologues, infirmires, kinsithrapeutes, dentistes, podologues, biologistes, psychologues, personnel de la croix rouge, coordinateur de lhpital) et les services de la Mairie ont travaill jour et nuit pour faire natre, en 3 jours, ce centre de consultations.</p><p>Voici les donnes cls : <ul style="list-style: circle"> <li>Ouverture le 20 mars, tous les jours de 9h a 18h avec un numro ddi : 3966</li><li>Pas de prise dinitiative sans un avis mdical</li><li>15  25 personnes sur place (personnels soignants, personnels dsinfectant les box aprs chaque consultation) et un Centre dappels constitu de 2  4 personnes.</li><li>Capacit de recevoir 250 personnes/jour avec des traducteurs de diverses origines</li><li>Un suivi  J+2,J+7 et J+14 pour les personnes confines</li><li>Des modles dordonnances pr-remplies</li><li>1  fiche patient Covid ARS </li><li>1 fiche de liaison standard dlivre  laccueil de lhpital</li><li>Une formation sur lhygine de 4H pour les intervenants</li></ul> </p><div class="line"></div><h3 id="realisationPart2">2. Centre Covid-19  Exemple de Centre  Recommandation de lARS</h3> <p><b>LARS des Hauts de France a tabli le 20 mars des lignes directrices</b> afin de rpondre aux besoins des professionnels dorganiser des locaux et des procdures permettant disoler la prise en charge des patients potentiellement infects COVID 19. Cette offre vient en soutien de la prise en charge des patients par leur mdecin traitant.</p><p><b>Ladressage sera fait par les SAMU,</b> rgulations librales, plateforme de lAssurance Maladie, et mdecins traitants qui seront informs de lexistence des centres ddis et de la possibilit dy adresser des patients sur rendez-vous dans la mesure du possible. Le Samu reste le contact privilgi.</p><p><b>Le centre doit tre ouvert 7J/7 de prfrence de 8H  20H</b>. Cependant, cela est  moduler selon les besoins du territoire et les capacits dorganisation des professionnels. Possibilit denvisager des horaires de consultation ddis aux tlconsultations.</p><p><b>Pour lquipe soignante</b>, lARS recommande 1 mdecin gnraliste, 1 IDE ou soignant, 1 service dhygine et un service daccueil. Il est demand  la personne soccupant de laccueil, denvoyer quotidiennement  lARS le nombre de consultations ralises dans la journe ; lobjectif sera dadapter les organisations au plus prs de lvolution de lpidmie,  cette adresse mail : ARS-HDF-DEFENSE@ars.sante.fr</p><p><b>Pour lamnagement des locaux</b> : une salle dattente o lon sassure dun  deux mtres entre les chaises. Ne pas mettre de magazines, ni prospectus ni jouets. Sil y a 2 locaux de consultation, en fonction de lactivit, possibilit dalterner leur utilisation pour le temps de la dsinfection. En dehors dune impossibilit de consulter seul, le patient ne doit pas tre accompagn pour se rendre  sa consultation. <b>AUCUN DPISTAGE NE DOIT TRE REALIS EN AMBULATOIRE.</b></p><p>Equipement et procdures dhygine : Masques de protection, Lavage des mains au savon ou au solut hydro alcoolique, Blouse (a laver tous les soirs a 60 ou a remplacer) ou tenue spcifique  mettre en arrivant au centre et  retirer avant de repartir (pour lavage  60), dsinfection du bureau et de la table dexamen aprs chaque passage, Bio dsinfection 1 a 2 fois par jour.</p><p>Dans quelles structures installer le centre ? Dans une maison mdicale de garde au sein dun tablissement de sant ou dans un autre local mis  disposition par un tablissement de sant. Dans une maison de sant pluriprofessionnelle, centre de sant, local dans un hpital de proximit, dans un local ddie, pouvant sappuyer sur le maillage sanitaire existant.</p><p>Modalits daccompagnement de lARS Hauts de France : En fonction des besoins des patients sur les territoires et des organisations professionnelles, lARS Hauts de France pourra accompagner financirement la mise en place des centres ddis COVID 19 et des flux ddis COVID 19 au sein des structures dexercice coordonn, afin de compenser les frais engags par les professionnels, de la faon suivante : <ul style="list-style: circle">Sur la base dun fonctionnement 7 jours sur 7, de 8h  20h, pendant 2 mois : <li>Forfait temps soignant : 2 ETP pour un fonctionnement 7/7 de 8h a 20h : 13 500  pour deux mois</li><li>Forfait accueil / secrtariat : 2 ETP pour un fonctionnement 7/7 de 8h a 20h : 12 000  pour deux mois</li><li>Forfait hygine : 2 ETP pour un fonctionnement 7/7 de 8h a 20h : 6 500  pour deux mois</li><li>Forfait de compensation de participation mdicale COVID 19 : 12 000  par structure pour deux mois.</li></ul> </p></div><div class="tab-pane fade show" id="cellule" role="tabpanel" aria-labelledby="cellule-tab"> <h2>Fonctionnement d\'une cellule de crise</h2> <h3>Exemple dinitiative locale - Cellule de Crise en Mairie</h3> <p>Afin de lutter efficacement contre l\'pidmie, il est ncessaire de coordonner toutes les forces locales afin de crer de la synergie dans les politiques publiques dveloppes pour lutter contre lpidmie. Nous proposons la mise en place dune cellule de crise en Mairie sous le prisme de la mthode du questionnement (Q.Q.O.C.Q). Un guide pour les initiatives locales est disponible <a href="images/assets/documents/CELLULE_DE_CRISE_COLLECTIVITE_TERRITORIALE_-_covid-19_-_mars_2020.pdf" download="CELLULE_DE_CRISE_COLLECTIVITE_TERRITORIALE_-_covid-19_-_mars_2020.pdf" target="_blank">ici</a>.</p><img class="d-block mx-auto" width="80%" src="images/assets/images/table2.png"> </div><div class="tab-pane fade show" id="numerique" role="tabpanel" aria-labelledby="numerique-tab"> <h2>Solution numrique de pilotage de l\'ensemble du parcours : CRISISOFT</h2> <h3 id="numeriquePart1">1. Donnes de rfrence</h3> <p>Le schma de donnes de rfrence collectes lors de laccueil est renseign dans le formulaire consultable sur ce lien : formulaire de donnes : <a href="/#/ressources" class="btn btn-sm cov-btn-blue-light" role="button" aria-pressed="true"> <img src="images/assets/icon/download-w.png" width="32px;"> CENTRE DE TRIE </a> </p><div class="line"></div><h3 id="numeriquePart2">2. La solution CRISISOFT comme solution numrique</h3> <p>CrisiSoft met  disposition entirement gratuitement et sans engagement une version simplifie de sa solution informatique de gestion oprationnelle et cooprative de gestion de crises sanitaires auprs des ARS, des SAMU et des services spcifiques sur le territoire mtropolitain et pendant toute la dure de la crise CoVid-19. Notre solution vise  rpondre  quelques-unes des problmatiques trs oprationnelles des futurs centres CoVid-19 et des enjeux de supervision par les collectivits locales et les ARS : <ul> <li>Scuriser et garantir l\'intgrit de la filire CoVid-19.</li><li>Recenser prcisment et en temps rel les patients CoVid-19 en fonction de la gravit des cas, de leur orientation sur les structures spcifiques CoVid-19 (Prlvement, Suivi), les services spcialiss, leur confinement  domicile...</li><li>Connatre l\'tat de lactivit en temps rel de la filire locale, dpartementale ou rgionale et les flux de patients de chacune dentre elles.</li><li>changer et consigner rapidement des informations cruciales avec l\'ensemble de la filire via une main-courante partage.</li><li>Informer rapidement les partenaires, collaborateurs, instances sans saturer les moyens de communication, sans gaspiller de la ressource humaine cruciale et sans alourdir les tches administratives.</li></ul> </p><p>CrisiSoft a mis en place un dispositif exceptionnel pour les accompagner sur la dure de cette crise. <ul> <li>Fonctionnalits simplifies : nous avons retravaill nos interfaces et nos process techniques pour permettre une appropriation immdiate du logiciel par les oprateurs.</li><li>Dploiement rapide : 1 jour maximum par nos quipes  distance.</li><li>Prise en charge de l\'ensemble des cots : licence, hbergement scuris des donnes, configuration et paramtrage.</li><li>Journe d\'information/formation.</li><li>Tutoriels d\'utilisation.</li><li>Hotline fonctionnelle et technique.</li></ul> </p><p>Si la solution devait tre retenue dans le cadre ce projet, il faudrait faire voluer le dispositif pour rpondre  lampleur des dploiements envisags et  trouver les ressources financires pour ce faire.</p><p class="text-center"> <a href="/#/ressources" class="btn btn-sm cov-btn-blue-light" role="button" aria-pressed="true"> <img src="images/assets/icon/download-w.png" width="32px;"> Dcouverte du primtre<br class="d-inline d-sm-none"/>fonctionnel MenKorn CoVid-19 </a> </p></div></div></div><div class="bg-light border-left order-2 order-lg-1" id="sidebar-wrapper"> <div class="sticky-top sticky-offset"> <div class="sidebar-heading">Guide pratique</div><div class="list-group list-group-flush" role="tablist nav nav-tabs"> <a class="list-group-item list-group-item-action bg-light nav-link active" id="introduction-tab" data-toggle="tab" href="#introduction" role="tab" aria-controls="introduction" aria-selected="true">Introduction</a> <a class="list-group-item list-group-item-action bg-light nav-link" id="objectifs-tab" data-toggle="tab" href="#objectifs" role="tab" aria-controls="introduction" aria-selected="true">Objectifs</a> <a class="list-group-item list-group-item-action bg-light nav-link" id="macro-tab" data-toggle="tab" href="#macro" role="tab" aria-controls="introduction" aria-selected="true">Chane et Macro</a> <a class="list-group-item list-group-item-action bg-light nav-link" id="description-tab" data-toggle="tab" href="#description" role="tab" aria-controls="introduction" aria-selected="true">Gnralits</a> <a class="list-group-item list-group-item-action bg-light nav-link" id="organisation-tab" data-toggle="tab" href="#organisation" role="tab" aria-controls="introduction" aria-selected="true">Centres de tri</a> <a class="list-group-item list-group-item-action bg-light nav-link" id="plans-tab" data-toggle="tab" href="#plans" role="tab" aria-controls="introduction" aria-selected="true">Plans Physiques</a> <a class="list-group-item list-group-item-action bg-light nav-link" id="realisation-tab" data-toggle="tab" href="#realisation" role="tab" aria-controls="introduction" aria-selected="true">Ralisations et directives en cours</a> <a class="list-group-item list-group-item-action bg-light nav-link" id="cellule-tab" data-toggle="tab" href="#cellule" role="tab" aria-controls="introduction" aria-selected="true">Cellules de crise</a> <a class="list-group-item list-group-item-action bg-light nav-link" id="numerique-tab" data-toggle="tab" href="#numerique" role="tab" aria-controls="introduction" aria-selected="true">Solution numrique</a> </div><div class="sidebar-heading mt-2"> <a class="github-link" href="https://github.com/salym/covilience" target="_blank"> <img class="float-left mr-1" src="images/assets/icon/github.png" height="32px" width="32px"> ditez-moi sur Github </a> </div></div></div></div></main>';

