import React, { Component } from "react";

import queryString from "query-string";

import { withRouter } from "react-router-dom";

import { Grid, Typography } from "@material-ui/core";

import Alert from '@material-ui/lab/Alert';

import ProductCard from '../ProductCard';
import ProductBasket from '../ProductBasket';
import ProductShoppingCard from '../ProductShoppingCard';
import Loader from '../Loader';
import Footer from "../Footer";

import AdBlockDetect from 'react-ad-block-detect';

import authentication from "../../services/authentication";

import { withStyles } from "@material-ui/core/styles";

/************************************************************************************************

                                        DANGER ZONE

************************************************************************************************/

// import products from "../../services/products-test"; // Development
import products from "../../services/products"; // Production

import { loadStripe } from '@stripe/stripe-js';

/***********************************************************************************************/

const styles = (theme) => ({
    grid: {
        margin: "50px 0 80px 0",
        width: "100%",
    },
});

class ProductPage extends Component {

    constructor(props) {
        super(props);

        this.countersFormatted = {};

        this.state = {
            order: {
                uid: null,
                pending: null,
                facturation: null,
            },
            openCart: false,
            counters: this.countersFormatted,
            comment: "",
            loader: false,
            uiDisabled: false,
        };
    };

    componentWillMount() {

        const queryParsed = queryString.parse(this.props.location.search);
        let queryOrderId = queryParsed.orderId;
        let queryState = queryParsed.state;

        if (queryOrderId !== undefined && queryState == "cancel") {

            // console.log("Revient d'un cancel");

            authentication
                .updateOrder(queryOrderId, { "cancel": true, "pending": true })
                .then(result => {
                    // console.log("Order updated to cancel : true");
                });

            authentication
                .deleteFees(queryOrderId)
                .then(result => {
                    // console.log("Fees deleted " + result);
                });
        }

        if (this.props.user !== null) {
            this.setState({
                ...this.state,
                loader: true,
            });
        }

        products.map((item) => {
            this.countersFormatted[item.sku] = 0;
            return null;
        });

        // Get the last order if it exists
        authentication
            .getPendingOrder()
            .then((orderDocument) => {
                // console.log("Pending order :", orderDocument);

                if (orderDocument != null) {
                    this.setState({
                        ...this.state,
                        order: {
                            ...orderDocument
                        }
                    });

                    // Get les products du order
                    authentication
                        .getProducts(orderDocument.uid)
                        .then(productDocuments => {
                            // console.log("Order's products :", productDocuments);

                            if (productDocuments == null) {
                                this.setState({
                                    ...this.state,
                                    loader: false
                                });
                                return;
                            };

                            // Updater les quantits des produits dans le state
                            productDocuments.map((item) => {

                                // console.log("Produit " + item.uid + ", quantit " + item.quantity);

                                this.setState({
                                    ...this.state,
                                    loader: false,
                                    counters: {
                                        ...this.state.counters,
                                        [item.uid]: item.quantity || 0,
                                    }
                                });

                                return true;
                            });
                            // console.log("State :", this.state);
                        });
                } else {
                    this.setState({
                        ...this.state,
                        loader: false,
                    });
                }
            });
    };

    handleRedirect = async (stripeItems) => {
        // console.log("stripeItems :", stripeItems);

        /************************************************************************************************

                                        DANGER ZONE

        ************************************************************************************************/

        // const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_TEST); // Development
        const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_PRODUCTION); // Production

        /***********************************************************************************************/

        this.setState({ ...this.state, loader: true })

        const stripe = await stripePromise;

        // Redirect to stripe
        const { error } = await stripe.redirectToCheckout({
            items: stripeItems,
            successUrl: 'https://covilience.org/#/commandes?orderId=' + this.state.order.uid + '&state=success',
            cancelUrl: 'https://covilience.org/#/produits?orderId=' + this.state.order.uid + '&state=cancel',
            customerEmail: this.props.user.email,
        });
    }

    handleCheckout = (event) => {

        // Si user non connect, demander de se connecter
        if (this.props.user == null) {
            return this.props.onConnectClick();
        }

        let items = this.state.counters;

        let stripeItems = [];
        let fees;

        Object.entries(items).map((item) => {
            if (item[1] > 0) {
                stripeItems.push({ sku: item[0], quantity: item[1] });
                return null;
            }
        });

        // Met  jour le commentaire dans le order
        authentication
            .updateOrder(this.state.order.uid, { "comments": { "beforeCheckout": this.state.comment } })
            .then(result => {
                // console.log("Comment added");
            });

        this.handleRedirect(stripeItems);
    };

    openCartDetails = () => {
        this.setState({
            ...this.state,
            openCart: true
        }, () => {

            // Dans le cas o il est arriv  la page de stripe
            // mais n'a ni completed or cancelled et a ferm la page
            authentication
                .deleteFees(this.state.order.uid)
                .then(result => {
                    // console.log("Fees deleted " + result);
                });
        })
    };

    closeCartDetails = () => {
        this.setState({
            ...this.state,
            openCart: false
        });

        authentication
            .deleteFees(this.state.order.uid)
            .then(result => {
                // console.log("Fees deleted " + result);
            });
    };

    updateProduct = (counterId, newQuantity) => {

        // console.log("Update product quantity");

        if (newQuantity < 1) {
            /*console.log("%cDisable UI", "background:red; color:white; font-size:20px");
            this.setState({
                ...this.state,
                uiDisabled: true,
                loader: true,
            });*/
            // Delete product in firebase
            authentication
                .deleteProduct(this.state.order.uid, counterId)
                .then(result => {
                    /*console.log("%cEnable UI", "background:orange; color:white; font-size:20px");
                    this.setState({
                        ...this.state,
                        uiDisabled: false,
                        loader: false,
                    });*/
                    // console.log("Product deleted");
                });
        } else {

            // Update product in firebase
            authentication
                .updateProduct(this.state.order.uid, counterId, newQuantity)
                .then(result => {
                    // console.log("Product updated");
                });
        }

        // Calculer le nombre de produits slectionns
        let itemNumber = Object.values(this.state.counters).reduce((a, b) => a + b, 0);

        if (itemNumber < 1) {

            // console.log("%cDisable UI", "background:blue; color:white; font-size:20px");
            this.setState({
                ...this.state,
                uiDisabled: true,
                loader: true,
            });

            // S'il n'y a aucun produit, supprimer l'order et ses produits
            authentication
                .deleteOrder(this.state.order.uid)
                .then(result => {
                    // console.log("Order deleted");
                    // console.log("%cEnable UI", "background:blue; color:white; font-size:20px");

                    this.setState({
                        ...this.state,
                        order: {
                            uid: null,
                        },
                        uiDisabled: false,
                        loader: false,
                    });
                });

        }

    }

    handleChange = (counterId, newValue) => {

        // Si user non connect, demander de se connecter
        if (this.props.user == null) {
            return this.props.onConnectClick();
        }

        // console.log("%cChangement des quantit", "background:yellow; color:white; font-size:20px");

        // console.log(this.state.counters);

        // console.log(counterId, newValue);

        if (parseInt(newValue) < 0 && newValue.toString().length > 0) return;

        if (newValue.toString().includes(".") || newValue.toString().includes(",")) return;

        let newQuantity = newValue === "" ? 0 : parseInt(newValue);

        this.setState({
            ...this.state,
            counters: {
                ...this.state.counters,
                [counterId]: +newQuantity
            }
        }, () => {

            // console.log("State updated :", this.state);

            if (this.state.order.uid == null) {

                // Desactiver les boutons de changement de quantit
                // Empecher qu'il recrer un autre order pending
                // console.log("%cDisable UI", "background:blue; color:white; font-size:20px");
                this.setState({
                    ...this.state,
                    uiDisabled: true,
                    loader: true,
                });

                // Crer un order dans firebase
                authentication
                    .createOrder()
                    .then((newOrder) => {
                        // console.log("Order created :", newOrder);

                        this.setState({
                            ...this.state,
                            order: newOrder
                        }, () => {

                            // Ractiver les boutons de changement de quantit
                            // console.log("%cEnable UI", "background:blue; color:white; font-size:20px");
                            this.setState({
                                ...this.state,
                                uiDisabled: false,
                                loader: false,
                            });
                            // Met  jour les produits dans firebase
                            this.updateProduct(counterId, newQuantity);
                        });
                    });

            } else {

                // Met  jour les produits dans firebase
                this.updateProduct(counterId, newQuantity);
            }


        });
    };

    handleComment = (value) => {
        this.setState({
            ...this.state,
            comment: value
        });
    }

    render() {
        const { classes } = this.props;
        const { loader } = this.state;

        return (
            <>
                <div dangerouslySetInnerHTML={{ __html: '<section class="container-fluid" style="position: relative; height: 320px; background-image: linear-gradient(var(--blue), var(--blue-light));"> <div class="row justify-content-center" style="height: 100%;"> <h1 class="col-12 text-center align-self-center align-middle text-white" style="font-size:24px">Pour vous aider  vous protger au mieux, nous avons selectionn les fabricants qui rpondent aux critres de qualit les plus stricts et avons sollicit les services d\'une entreprise de contrle qualit indpendante.</h1> <svg style="position: absolute; left:0; bottom: 0; width: 100%; height: 60px;" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1416.99 174.01" preserveAspectRatio="none"> <path style="" fill="#fafafa" d="M0,280.8S283.66,59,608.94,163.56s437.93,150.57,808,10.34V309.54H0V280.8Z" transform="translate(0 -135.53)"/></svg> </div></section>' }} />

                <AdBlockDetect>
                    <Grid className={classes.grid} container justify="center" spacing={5}>
                        <Grid item>
                            <Alert style={{ fontSize: '1.5em' }} variant="filled" severity="error">Vous utilisez un bloqueur de pub, celui-ci bloque les fonctions lies aux commandes,  la cration ou  l'accs aux comptes. <br />Veuillez le dsactiver ou utiliser un autre navigateur.</Alert>
                        </Grid>
                    </Grid>
                </AdBlockDetect>

                <Grid className={classes.grid} container justify="center" spacing={5}>

                    {loader ? <Loader /> : null}

                    {products.map((item, index) =>
                        <ProductCard
                            key={index}
                            counter={this.state.counters[item.sku]}
                            product={item}
                            onChange={this.handleChange}
                            uiDisabled={this.state.uiDisabled}
                        />
                    )}

                    <ProductBasket
                        counters={this.state.counters}
                        handleOpen={this.openCartDetails}
                    />

                    <ProductShoppingCard
                        counters={this.state.counters}
                        products={products}
                        openCart={this.state.openCart}
                        onChange={this.handleChange}
                        handleClose={this.closeCartDetails}
                        comment={this.state.comment}
                        handleComment={this.handleComment}
                        handleCheckout={this.handleCheckout}
                        uiDisabled={this.state.uiDisabled}
                    />
                </Grid>
                <Grid className={classes.grid} container justify="center" spacing={5}>
                    <Typography variant="caption" style={{ fontStyle: "italic" }}>
                        Certaines photos reprsentent des illustrations et sont non contractuelles - elles peuvent diffrer du produit rel dans les formes et les couleurs.
                    </Typography>
                </Grid>
                <Footer />
            </>
        );
    };
}

export default withRouter(withStyles(styles)(ProductPage));
